.page {
  height: 100%;
}

.page__body {
  height: 100%;
  min-width: 320px;
  margin: 0;
  font-family: "PT Sans", sans-serif;
  background-color: @bg-first-color;
}

.page__body-container {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  background-color: @light-color;
  max-width: 1200px;
  box-shadow: 0 0 16px #cccccc;
}

.page__main {
  flex-grow: 1;
}
